header {
  padding: 30px;
  height: 100vh;
}

.headingText {
  font-size: 1.8rem;
  line-height: 1.6;
  text-align: center;
  margin: 20px auto;
  width: 50vw;
}

.headerEls {
  display: flex;
  justify-content: space-between;
  height: 70vh;
  margin: 30px 0;
}

.newNotice {
  align-self: flex-start;
  height: 400px;
  width: 400px;
  background-color: #f7e76f;
  border-radius: 10px;
  padding: 20px;
  position: relative;
  margin-top: 30px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.plus {
  background: none;
  border: none;
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 2.5rem;
  color: #f50057;
  cursor: pointer;
  transition: all 0.3s;

  &:hover,
  &:focus {
    transform: scale(1.4);
  }

  &:focus {
    border: 1px dotted #f50057;
  }
}

input,
textarea {
  background-color: rgba(255, 255, 255, 0.6);
  border: none;
  width: 360px;
  padding: 10px;
  border-radius: 5px;
}

.name,
.newMessage,
.senderName {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.6rem;
}

.name {
  margin-top: 15px;
  height: 30px;
}

.newMessage {
  margin: 15px 0;
  height: 235px;
  resize: none;
}

.senderName {
  height: 30px;
}

.required {
  text-align: right;
  margin: 5px 0;
}

.illustration {
  align-self: flex-end;
}

@media (min-width: 2200px) {
  header {
    height: 50vh;
  }

  .headerEls {
    height: 70%;
  }

  .newNotice {
    height: 550px;
    width: 550px;
  }

  input,
  textarea {
    width: 510px;
  }

  .newMessage {
    height: 385px;
  }
}

@media (max-width: 1350px) {
  .newNotice {
    height: 350px;
    width: 350px;
  }

  input,
  textarea {
    width: 310px;
  }

  .newMessage {
    height: 185px;
  }
}

@media (max-width: 1310px) {
  .headingText {
    width: 60vw;
  }
}

@media (max-width: 1200px) {
  header {
    height: auto;
  }
  .headerEls {
    height: auto;
  }
  .newNotice {
    height: 300px;
    width: 300px;
  }

  input,
  textarea {
    width: 260px;
  }

  .newMessage {
    height: 135px;
  }
}

@media (max-width: 1090px) {
  .headingText {
    width: 70vw;
  }
}

@media (max-width: 935px) {
  .headingText {
    width: 80vw;
  }
}

@media (max-width: 896px) {
  header {
    height: auto;
  }

  .headerEls {
    flex-direction: column;
    margin-bottom: 0;
  }
  
  .newNotice,
  .illustration {
    align-self: center;
  }

  .illustration {
    margin: 70px 0 30px 0;
  }
}

@media (max-height: 700px) {
  header {
    height: auto;
  }
 .headerEls {
   height: auto;
 }
}

@media (max-width: 365px) {
  .newNotice {
    height: 275px;
    width: 275px;
  }
  input,
  textarea {
    width: 235px;
  }
  .newMessage {
    height: 115px;
  }
}
